___CSS_LOADER_EXPORT___.push([module.id, `/* Reset and base styles  */

* {
    margin: 0;
    padding: 0;
    border: none;

}

*,
*::before,
*::after {
    box-sizing: border-box;
}
/* Links */

a, a:link, a:visited {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}
/* Common */

aside, nav, footer, header, section, main {
    display: block;
}

h1, h2, h3, h4, h5, h6, p {
    font-weight: inherit;
    font-size: inherit;
}

ul, li {
    list-style: none;
}

img {
    vertical-align: top;
}

img, svg {
    height: auto;
    max-width: 100%;
}

address {
    font-style: normal;
}
/* Form */

input, textarea, button, select {
    color: inherit;
    font-size: inherit;
    font-family: inherit;
    background-color: transparent;
}

input::-ms-clear {
    display: none;
}

button, input[type="submit"] {
    display: inline-block;
    background: none;
    background-color: transparent;
    box-shadow: none;
    cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
    outline: none;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

label {
    cursor: pointer;
}

legend {
    display: block;
}
