import React, { useRef } from 'react';
import { useStateCustom } from '@packages/hooks';
import { AnimatePresence, Button, Input } from '@packages/ui';

import { useModules } from '@/features';
import { constants } from '@/shared/utils';

import styles from './styles.module.scss';

const Modules = () => {
    const ref = useRef(null);
    const file = useStateCustom(null);
    const { downloadModule, getModules, removeLocalModule, removeRemoteModule, activate, addRemoteModuleUrl } = useModules();
    const { mutate: downloadBuildMutate } = downloadModule();
    const { mutate: addRemoteModuleUrlMutate } = addRemoteModuleUrl();
    const { mutate: removeLocalModuleMutate } = removeLocalModule();
    const { mutate: removeRemoteModuleMutate } = removeRemoteModule();
    const { mutate: activateMutate } = activate();
    const { data: modulesData, refetch } = getModules();

    const remoteModuleUrlInput = Input.use({});

    const handleDownloadBuild = () => {
        downloadBuildMutate(file.value, {
            onSuccess: () => {
                file.set(null);
                ref.current.value = '';
                refetch();
            },
        });
    };

    const handleAddRemoteModuleUr = () => {
        addRemoteModuleUrlMutate(
            { url: remoteModuleUrlInput.value },
            {
                onSuccess: () => {
                    remoteModuleUrlInput.setValue('');
                    refetch();
                },
            }
        );
    };

    const handleRemove = (type: 'local' | 'remote', data: any) => {
        switch (type) {
            case 'local':
                removeLocalModuleMutate(data, {
                    onSuccess: () => {
                        refetch();
                    },
                });

                break;

            case 'remote':
                removeRemoteModuleMutate(data, {
                    onSuccess: () => {
                        refetch();
                    },
                });
        }
    };

    const handleActivate = (data: any) => {
        activateMutate(data, {
            onSuccess: () => {
                refetch();
            },
        });
    };

    return (
        <div className={styles.wrapper}>
            <div className={styles.title}>Модули</div>
            <div className={styles.download}>
                <div className={styles.downloadBuild}>
                    <div>Загрузить сборку</div>
                    <div className={styles.controls}>
                        <input
                            ref={ref}
                            type={'file'}
                            accept=".zip"
                            onChange={(e) => {
                                const formData = new FormData();
                                formData.append('file', e.target.files[0]);
                                file.set(formData);
                            }}
                        />
                        <AnimatePresence visible={!!file.value} className={styles.sendBtn}>
                            <Button onClick={handleDownloadBuild}>отправить</Button>
                        </AnimatePresence>
                    </div>
                </div>
                <div className={styles.downloadUrl}>
                    <div>Добавить удаленный модуль</div>
                    <div className={styles.controls}>
                        <Input inputAttrs={remoteModuleUrlInput.inputAttrs} />
                        <AnimatePresence visible={!!remoteModuleUrlInput.value.length} className={styles.sendBtn}>
                            <Button onClick={handleAddRemoteModuleUr}>отправить</Button>
                        </AnimatePresence>
                    </div>
                </div>
            </div>

            <div className={styles.modules}>
                {modulesData &&
                    Object.entries(modulesData)?.map(([key, module]: any) =>
                        module.versions.length ? (
                            <div className={styles.module} key={module.name}>
                                <div className={styles.type}></div>
                                <div className={styles.name}>
                                    {module.name} / {module.type}
                                </div>
                                <div className={styles.builds}>
                                    {module.versions.map((version: any) => {
                                        const latest = module.latest.url === version.url;
                                        const data = { name: module.name, ...version };

                                        return (
                                            <div key={version.version} className={styles.build}>
                                                <div className={styles.buttons}>
                                                    <div className={styles.version}>v_{version.version}</div>
                                                    <Button
                                                        style={{ backgroundColor: latest ? 'green' : '' }}
                                                        disabled={latest}
                                                        onClick={() => handleActivate(data)}
                                                    >
                                                        {latest ? 'активна' : 'активировать'}
                                                    </Button>
                                                    <Button style={{ backgroundColor: 'red' }} onClick={() => handleRemove(module.type, data)}>
                                                        удалить
                                                    </Button>
                                                </div>
                                                <div className={styles.link} onClick={() => window.open(version.url, '_blank')}>
                                                    {version.url}
                                                </div>
                                            </div>
                                        );
                                    })}
                                </div>
                            </div>
                        ) : null
                    )}
            </div>
        </div>
    );
};

export default Modules;
